<template>
  <div id="home_repo-page">
    <div class="wrapper df">
      <div class="column-1 df dfv">
        <div class="left-1"></div>
        <div class="left-2"></div>
        <div class="left-3"></div>
        <div class="left-4"></div>
      </div>
      <div class="column-2 df dfv">
        <div class="alarm-map"></div>
        <div class="center-1"></div>
      </div>
      <div class="column-3 df dfv">
        <div class="right-1"></div>
        <div class="right-2"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  }
}
</script>

<style lang="less" scoped>
.wrapper{
  height: calc(100% - 20px);
  padding: 10px;
}
.column-1{
  width: 280px;
  margin-right: 10px;
}
.column-3{
  width: 350px;
  margin-right: 10px;
}
.column-2{
  flex: 1;
}
.left-1, .left-2, .left-3, .left-4{
  background-color: #fff;
  flex: 1;
}
.right-1{
  flex: 4;
  background-color: #fff;
} 
.right-2{
  flex: 5;
  background-color: #fff;
}
.left-1, .left-2, .left-3, .right-1{
  margin-bottom: 10px;
}
.alarm-map{
  flex: 1;
  background-color: #fff;
  margin-right: 10px;
}
.center-1{
  margin-top: 10px;
  margin-right: 10px;
  height: 200px;
  background-color: #fff;
}
</style>


